import hondaLogo from '../images/hondalogo.svg';
import './Honda.css'
import {Link} from 'react-router-dom'
import Header2 from '../components/Header2';

function Honda() {
    return (
      <>
        <Header2/>
        <div className='divHondalogo'>
            <img src={hondaLogo} alt='honda' className='hondalogotipo'></img>
        </div>
      </>
    );
  }
  
  export default Honda;