import {Link} from 'react-router-dom';
import './Header1.css';

function Header1() {
    return (
      <>
      <div>
        <header className="header">
            <Link to='/harley' className='linkTo'><a className='harley'>HARLEY</a></Link>
            <Link to='/suzuki' className="linkTo"><a className='suzuki'>SUZUKI</a></Link>
            <Link to='/honda' className="linkTo"><a className='honda'>HONDA</a></Link>
            <Link to='/yamaha' className="linkTo"><a className='yahama'>YAMAHA</a></Link>
            <Link to='/lambreta' className="linkTo"><a className='lambreta'>LAMBRETA</a></Link>
        </header>
      </div>
      <Link to='/' className='linkTo'>Pagina inicial</Link>
      </>
    );
  }
  
  export default Header1;