import './Yamaha.css'
import {Link} from 'react-router-dom'

function Yamaha() {
    return (
      <>
        <Header1/>
        <div className='divYahamalogo'>
            <img src={YamahaLogo} alt='yamaha' className='yamahalogotipo'></img>
        </div>
      </>
    );
  }
  
  export default Yamaha;