import { HashRouter as BrowserRouter, Route, Switch } from 'react-router-dom'
import './App.css';
import MainPage from './routed/MainPage';
import Harley from './routed/Harley';
import Honda from './routed/Honda';
import Suzuki from './routed/Suzuki';
import Yamaha from './routed/Yamaha';
import Lambreta from './routed/Lambreta';
import City from './routed/City';
import Trail from './routed/Trail';
import Offroad from './routed/Offroad';
import Sport from './routed/Sport';
import Scooter from './routed/Scooter';

function App() {
  return (
    <div className="App">
      <header className="App-header">
        
        <BrowserRouter>
          <Switch>
            <Route path='/' exact={true}>
              <MainPage/>
            </Route>

            <Route path='/harley'  exact={true}>
              <Harley/>
            </Route>

            <Route path="/suzuki"  exact={true}>
              <Suzuki/>
            </Route>

            <Route path="/honda"  exact={true}>
              <Honda/>
            </Route>

            <Route path="/yamaha"  exact={true}>
              <Yamaha/>
            </Route>

            <Route path="/lambreta"  exact={true}>
              <Lambreta/>
            </Route>

            <Route path='/honda/city'>
              <City/>
            </Route>

            <Route path='/honda/trail'>
              <Trail/>
            </Route>

            <Route path='/honda/off-road'>
              <Offroad/>
            </Route>

            <Route path='/honda/sport'>
              <Sport/>
            </Route>

            <Route path='/honda/scooter'>
              <Scooter/>
            </Route>
            
          </Switch>
        </BrowserRouter>

      </header>
    </div>
  );
}

export default App;
