import Offroadimg from '../images/offroadhonda.png'

function Offroad() {
    return (
      <>
        <Header2/>
        <div className='divOffroad'>
            <img src={Offroadimg} alt='off-road' className='yamahalogotipo'></img>
        </div>
      </>
    );
  }
  
  export default Offroad;