import Header2 from '../components/Header2';
import Sportimg from '../images/sporthonda.png'

function Sport() {
    return (
      <>
        <Header2/>
        <div className='divSport'>
            <img src={Sportimg} alt='sport' className='yamahalogotipo'></img>
        </div>
      </>
    );
  }
  
  export default Sport;