import Header2 from '../components/Header2';
import TrailMoto from '../images/trailhonda.jpg'

function Trail() {
    return (
      <>
        <Header2/>
        <div className='divTraillogo'>
            <img src={TrailMoto} alt='trail' className='yamahalogotipo'></img>
        </div>
      </>
    );
  }
  
  export default Trail;