import Header1 from '../components/Header1';
import LambretaLogo from '../images/lambrettalogo.jpg';
import './Lambreta.css'
import {Link} from 'react-router-dom'

function Lambreta() {
    return (
      <>
        <Header1/>
        
        <div className='divLambretalogo'>
            <img src={LambretaLogo} alt='lambreta' className='lambretalogotipo'></img>
        </div>
        
      </>
    );
  }
  
  export default Lambreta;