import {Link} from 'react-router-dom';
import './Header2.css';

function Header2() {
    return (
      <>
      <div>
        <header className="header2">
            <Link to='/honda/city' className='linkTo'><a className='city'>3.1 CITY</a></Link>
            <Link to='/honda/trail' className="linkTo"><a className='trail'>3.2 TRAIL</a></Link>
            <Link to='/honda/off-road' className="linkTo"><a className='off-road'>3.3 OFF-ROAD</a></Link>
            <Link to='/honda/sport' className="linkTo"><a className='sport'>3.4 SPORT</a></Link>
            <Link to='/honda/scooter' className="linkTo"><a className='scooter'>3.5 SCOOTER</a></Link>
        </header>
      </div>
            <Link to='/' className='linkTo'>Pagina inicial</Link>
            <Link to='/honda' className='linkTo'><p>-Voltar</p></Link>
      </>
    );
  }
  
  export default Header2;