import Header1  from "../components/Header1";
import logoHarley from '../images/harleylogo.jpg';
import logoSuzuki from '../images/suzukylogo.png';
import logoHonda from '../images/hondalogo.svg';
import logoYahama from '../images/yamahalogo.jpg'
import logoLambreta from '../images/lambretalogo.png';
import './MainPage.css'

function MainPage() {
    return (
      <>
      <Header1/>
      <div className='divLogos1'>
        <img src={logoHarley} alt='harley' className='logos'></img>
        <img src={logoSuzuki} alt='suzuki' className='logos'></img>
        <img src={logoHonda} alt='honda' className='logoshonda'></img>
      </div>

      <div className='divLogos2'>
        <img src={logoYahama} alt='yamaha' className='logos'></img>
        <img src={logoLambreta} alt='lambreta' className='logos'></img>
      </div>
      </>
    );
  }
  
  export default MainPage;
  