import Header1 from '../components/Header1'
import harleyLogo from '../images/harleylogo.jpg'
import './Harley.css'
import {Link} from 'react-router-dom'

function Harley() {
    return (
      <>
        <Header1/>
        <div className='divHarleylogo'>
            <img src={harleyLogo} alt='harley' className='harleylogotipo'></img>
        </div>
      </>
    );
  }
  
  export default Harley;